.slide-box{
    background: -webkit-gradient(linear, left top, right top, color-stop(0, #1c3643), color-stop(100%, #273b47), to(#1e5372));
    background: -webkit-linear-gradient(left, #1c3643 0, #273b47 100%, #1e5372);
    background: -o-linear-gradient(left, #1c3643 0, #273b47 100%, #1e5372);
    background: linear-gradient(90deg, #1c3643 0, #273b47 100%, #1e5372);
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "img1 img2"
}
.slide-box img{
    height: 200px;
    width: 450px;
    -o-object-fit: cover;
       object-fit: cover;
}
.slide1{
    grid-area: img1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 1.2em;
}
.slide2{
    grid-area: img2;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 1.2em;
}
@media screen and (max-width: 576px){
    .slide-box{
        display: grid;
        grid-template-columns: 1fr;
        grid-template-areas: "img1";
    }
    .slide2{
        display:none;
    }
    .slide-box img{
        height: 150px;
    }
}
@media screen and (max-width: 425px){
    .slide-box img{
        height: 120px;
    }
}
@media screen and (max-width: 320px){
    .slide-box img{
        height: 100px;
    }
}


